.commentBox {
  border-top: 1px solid #efefef;
  padding: 15px 0; }
  @media (min-width: 320px) and (max-width: 450px) {
    .commentBox {
      display: none; } }
  .commentBox .input {
    border: 0;
    height: 20px;
    resize: none;
    width: 100%;
    font-size: 14px; }
    .commentBox .input:focus {
      outline: none; }
    .commentBox .input::placeholder {
      color: #999; }



